*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  user-select: none;
}

body {
  background-color: #fff;
  font-family: "TT Norms";
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
}

.containers {
  max-width: 1440px;
  padding: 0;
  margin: 0 auto;
  width: 100%;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
